* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  background: #000 url("img/bg.jpg") no-repeat center center/cover;
  height: 100vh;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
}

a {
  text-decoration: none;
}

.container {
  max-width: 1100px;
  margin: auto;
  padding: 0 20px;
}

.btn {
  display: inline-block;
  color: #fff;
  background-color: rgb(2, 51, 213);
  border-radius: 5px;
  font-size: 1em;
  text-align: center;
  padding: 10px 15px;
  border: 0;
  margin: 10px 0;
  cursor: pointer;
}

header {
  height: 30vh;
}

header img {
  width: 30vw;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.search {
  height: 20vh;
}
.search form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.search form .row {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 0.75rem;
}
input[type="text"] {
  display: block;
  padding: 6px;
  font-size: 16px;
  border: 0;
  border-radius: 5px;

  margin: auto 5px;
}
.big-size {
  width: 42vw;
}
.small-size {
  width: 18vw;
}
.cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1rem;
}

.card {
  cursor: pointer;
  background-color: transparent;
  height: 460px;
  perspective: 1000px;
}

.card h1 {
  font-size: 20px;
  border-bottom: 1px #fff solid;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

.card img {
  width: 100%;
  height: 350px;
  object-fit: cover;
}

.card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.card:hover .card-inner {
  transform: rotateY(180deg);
}

.card-front,
.card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
.card__front-title {
  height: 100px;
  background-color: rgb(2, 23, 98);
  box-shadow: inset 3px 2px 4px rgb(2, 51, 213);
}
.card__front-title h2 {
  padding: 12px 10px;
  color: #fff;
  font-size: 1.2rem;
  font-family: "Rokkitt", serif;
}
.card-back {
  background-color: #333;
  color: white;
  padding: 11px;
  transform: rotateY(180deg);
}

.card li {
  list-style: none;
  padding-bottom: 10px;
}
.short-text {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
@media (max-width: 800px) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
  .card__front-title h2 {
    font-size: 1.5rem;
  }
  header img {
    width: 40vw;
  }
}

@media (max-width: 500px) {
  .cards {
    grid-template-columns: 1fr;
  }
  .card__front-title h2 {
    font-size: 1.75rem;
  }
  header img {
    width: 50vw;
  }
  .cards {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .card {
    width: 60vw;
    height: 80vh;
  }
}
